@import "colors";
@import "standards";

:root {
   --noise: url("data:image/svg+xml,%3Csvg viewBox='0 0 400 400' xmlns='http://www.w3.org/2000/svg'%3E%3Cfilter id='noiseFilter'%3E%3CfeTurbulence type='fractalNoise' baseFrequency='9.97' numOctaves='6' stitchTiles='stitch'/%3E%3C/filter%3E%3Crect width='100%25' height='100%25' filter='url(%23noiseFilter)'/%3E%3C/svg%3E");
}

html {
   scroll-behavior: smooth;
}

body {
   @include font-family-serif;
   background-position: center -4rem !important;
   background-size: 16rem !important;
   margin: 0;
   padding: 1rem 0 1.5rem 0;
   padding: 1rem env(safe-area-inset-right) 0 env(safe-area-inset-left);
   position: relative;
}

.sans-serif {
   @include font-family-sans;
}

button,
.button,
cite,
input,
keygen select,
textarea {
   @include font-family-sans-rounded;
}

iframe {
   @include border-radius;
}

* {
   box-sizing: border-box;
}

hr {
   border-bottom: none;
   border-left: none;
   border-right: none;
   border-top: 0.05rem dashed var(--color-gray-2);
}

button,
.button {
   @include border-radius;
   border: none;
   cursor: pointer;
   display: inline-block;
   margin: 1rem 0.1rem 0 0;
   padding: 0.5rem 0.75rem;
   text-decoration: none;
   vertical-align: middle;

   &.selected {
      text-decoration: underline;
   }
}

.circled {
   @include border-radius;
   margin: 0 0.1rem 0 0;
   padding: 0.15rem 0.5rem;
}

.alert {
   background-color: var(--color-yellow-3);
   border-left: 0.5rem solid var(--color-yellow-2);
   color: var(--color-black);
   margin: 1rem 0 0 0;
   padding: 1rem;
}

a[href^="http://"]:not(.button),
a[href^="https://"]:not(.button) {
   &:after {
      background-image: url(/img/external-link.svg);
      background-position: bottom left;
      background-repeat: no-repeat;
      background-size: 80% 80%;
      content: "";
      display: inline-block;
      height: 0.9rem;
      margin-left: 0.25rem;
      opacity: 0.5;
      vertical-align: baseline;
      width: 0.9rem;
   }

   &:hover {
      &:after {
         opacity: 0.9;
      }
   }
}

.hideExternalIcon a:after,
.hideExternalIcon::after {
   background-image: unset !important;
   content: unset !important;
   display: none !important;
   height: 0;
   margin: 0;
   padding: 0;
   visibility: hidden !important;
   width: 0;
}

.outbound {
   margin: 0;
   padding: 0;
}

.outbund.button {
   display: inline-block;
   margin: 1rem 0.5rem 0 0;
}

meter {
   @include border-radius;
   margin: 0;
   overflow: hidden;
   padding: 0;
   width: 100%;

   background: var(--color-gray-2);

   &::-webkit-meter-inner-element {
      -webkit-appearance: inherit;
   }

   &::-webkit-meter-bar {
      background: none;
   }

   &::-webkit-meter-optimum-value {
      background: var(--color-primary);
   }

   &::-webkit-meter-suboptimum-value {
      background: var(--color-primary-complement);
   }

   &::-webkit-meter-even-less-good-value {
      background: firebrick;
   }
}

table {
   @include border-radius;

   @include mq("36", $type: max) {
      padding: 0 1rem 1rem 1rem;
   }

   @include mq("36", $type: min) {
      padding: 1rem;
   }

   thead {
      @include mq("36", $type: max) {
         display: none;
      }
   }

   tr {
      text-align: left;
      vertical-align: top;
   }

   th {
      font-size: 105%;

      &:nth-of-type(1) {
         @include mq("36", $type: min) {
            padding: 0 0 1rem 0;
         }
      }

      &:not(:nth-of-type(1)) {
         @include mq("36", $type: min) {
            padding: 0 0 1rem 1rem;
         }
      }
   }

   td {
      position: relative;

      &:before {
         @include mq("36", $type: max) {
            font-size: 0.75rem;
            font-weight: 700;
            left: 0;
            position: absolute;
            text-transform: uppercase;
            top: 0;
         }
      }

      @include mq("36", $type: max) {
         display: block;
         margin: 1.25rem 0 0 0;
         padding: 1.25rem 0 0 0;
      }

      &:nth-of-type(1) {
         @include mq("36", $type: min) {
            padding: 1rem 0 0 0;
         }
      }

      &:not(:nth-of-type(1)) {
         @include mq("36", $type: min) {
            padding: 1rem 0 0 1rem;
         }
      }
   }
}

#hiddenPolitics {
   &.hidden {
      display: none;
      visibility: hidden;
   }
}

.big5 {
   thead {
      th {
         @include mq("36", $type: max) {
            padding: 0;
            text-align: left;
         }

         @include mq("36", $type: min) {
            &:nth-of-type(1) {
               width: 50%;
            }
         }
      }
   }

   tbody {
      td {
         @include mq("36", $type: max) {
            &:nth-of-type(1):before {
               content: "Dimension";
            }

            &:nth-of-type(2):before {
               content: "Result";
            }

            &:nth-of-type(3):before {
               content: "Percentile";
            }

            &:nth-of-type(3) {
               text-align: center;
            }
         }

         @include mq("36", $type: min) {
            &:nth-of-type(1) {
               width: 50%;
            }

            &:nth-of-type(2) {
               width: calc(30% - 1rem);
            }

            &:nth-of-type(3) {
               width: calc(20% - 1rem);
            }
         }
      }
   }
}

.polCom {
   @include border-radius;
   padding: 0 1rem 1rem 1rem;

   dt {
      font-weight: 500;
      margin: 1rem 0 0 0;
      width: 100%;

      &:nth-of-type(1) {
         padding: 0;
      }

      &:not(:nth-of-type(1)) {
         padding: 1rem 0 0 0;
      }
   }

   dd {
      display: block;
      margin: 0;
      padding-top: 0.5rem;
      width: 100%;

      @include mq("44", $type: min) {
         align-items: center;
         display: grid;
         gap: 1rem;
         grid-template-columns: minmax(5rem, 9rem) minmax(5rem, 100%) minmax(5rem,
               10rem);
         justify-content: space-evenly;
         margin: 0;
         padding-top: 0;
         width: 100%;
      }

      meter {
         @include border-radius;
         margin: 0;
         overflow: hidden;
         padding: 0;
         width: 100%;

         background: var(--color-primary);

         &::-webkit-meter-inner-element {
            -webkit-appearance: inherit;
         }

         &::-webkit-meter-bar {
            background: none;
         }

         &::-webkit-meter-optimum-value {
            background: var(--color-primary-complement);
         }

         &::-webkit-meter-suboptimum-value {
            background: var(--color-primary-light);
         }

         &::-webkit-meter-even-less-good-value {
            background: var(--color-secondary-light);
         }
      }

      p {
         margin: 0;
      }
   }
}

.profileContainer {
   @include mq("36", $type: max) {
      display: block;

      .profilePic {
         margin: 1rem auto 0 auto;
      }

      .profileText {
         margin: 0 auto;
         text-align: center;

         ul,
         li {
            display: inline-block;
         }
      }
   }

   @include mq("36", $type: min) {
      display: grid;
      gap: 1rem;
      grid-template-columns: 300px [img] auto [text];

      .profilePic {
         grid-template: img;
      }

      .profileText {
         grid-template: text;
      }
   }

   @include mq("61", $type: min) {
      display: block;
   }
}

.profilePic {
   aspect-ratio: 150/91;
   max-width: 300px;
   overflow: hidden;
   position: relative;
   text-align: center;

   img {
      @include border-radius;
      height: 100%;
      width: 100%;
   }

   small {
      @include border-radius;
      border-bottom-left-radius: 0;
      border-top-left-radius: 2rem;
      border-top-right-radius: 0;
      background: rgba(0, 0, 0, 0);
      bottom: 0;
      color: white;
      cursor: pointer;
      opacity: 0.5;
      padding: 0.5rem 0.75rem 0.5rem 1rem;
      position: absolute;
      right: 0;
      text-shadow: 0 0 0 rgba(255, 255, 255, 0);
      z-index: 2;

      &:hover {
         animation: 1s ease-in 0s infinite alternate running glowing;
         background: rgba(0, 0, 0, 0.5);
      }

      &.animation {
         animation: 4s ease-out 0s infinite alternate running glowing;

         @keyframes glowing {
            0% {
               color: white;
               text-shadow: 0 0 0 rgba(255, 255, 255, 0);
            }

            50% {
               text-shadow: 0 0 0.25rem rgba(255, 255, 255, 1);
            }

            100% {
               color: var(--color-primary);
               text-shadow: 0 0 0 rgba(255, 255, 255, 0);
            }
         }
      }
   }
}

.smallUL-1 {
   margin: 0.75rem 0 0 0;
   padding: 0;

   li {
      display: inline-block;
      font-size: 80%;
      list-style: none;
      margin: 0.25rem 0 0 0;
   }
}

.smallUL-2 {
   margin: 0.75rem 0 0 0;
   padding: 0;

   li {
      display: table;
      font-size: 80%;
      list-style: none;
      margin: 0.25rem 0 0 0;
   }
}

header {
   margin: 1rem 0 0 0;
   position: relative;
   text-align: left;

   @include mq("44", $type: max) {
      padding: 1rem;
      padding: 1rem env(safe-area-inset-right) 1rem env(safe-area-inset-left);
   }

   @include mq("44") {
      padding: 1rem 2rem 0 1.5rem;
      padding: 1rem env(safe-area-inset-right) 0 env(safe-area-inset-left);
      vertical-align: baseline;
   }

   &:not(.home) {
      h1:hover {
         &::after {
            display: none;
         }
      }
   }

   h1 {
      @include font-family-title;
      background-clip: text;
      -webkit-background-clip: text;
      font-size: clamp(1rem, 5vw, 5rem);
      font-weight: 300;
      letter-spacing: clamp(0.01rem, 3vw, 2rem);
      margin: 0 0 0 clamp(-0.01rem, -3vw, -2rem);
      max-width: 78rem;
      padding: 0 0 0 clamp(0.01rem, 3vw, 2rem);
      position: relative;
      text-align: center;
      text-indent: clamp(0.01rem, 3vw, 2rem);
      text-transform: uppercase;
      z-index: 1;

      &::after {
         background-image: var(--noise);
         background-clip: text;
         -webkit-background-clip: text;
         content: 'DH:PERSONAL';
         height: 100%;
         left: 0;
         letter-spacing: clamp(0.01rem, 3vw, 2rem);
         mix-blend-mode: overlay;
         padding: 0 0 0 clamp(0.3rem, 1.5vw, 1rem);
         position: absolute;
         text-align: center;
         top: 0;
         width: 100%;
         z-index: 0;
      }

      a,
      a:link,
      a:hover,
      a:active,
      a:visited {
         color: inherit !important;
         text-decoration: none;
      }

      span {
         letter-spacing: 0;
      }
   }

   cite {
      display: block;
      margin: 1rem 0 0 0;
      transition: color 1.5s;
   }
}

.hideNav {
   margin: 0 auto;
   max-width: 78rem;
   position: relative;

   @include mq("33", $type: max) {
      bottom: 0.9rem;
      position: fixed;
      right: 1rem;
      text-align: right;
      top: unset;
      z-index: 9;
   }

   #toggleNav {
      font-size: 0.9rem;
      margin: 0;
      min-width: 2.75rem;
      top: 0.075rem;
      z-index: 8;

      @media screen and (min-width: 32.9rem) and (max-width: 55rem) {
         position: fixed;
         right: 2rem;
         top: 1.08rem;
      }

      @include mq("55") {
         position: absolute;
         right: 2rem;
      }
   }
}

nav {
   @include font-family-sans-rounded;
   align-content: flex-start;
   font-size: 1rem;
   height: 100%;
   left: 0;
   margin: 0;
   padding: 0;
   opacity: 1;
   text-align: left;
   top: 0;
   visibility: visible;
   width: 100%;
   z-index: 6;

   &.hidden {
      display: none;
      visibility: hidden;
   }

   @include mq("55", $type: max) {
      position: fixed;
   }

   @include mq("55") {
      position: absolute;
   }

   .container {
      display: flex;
      flex-flow: row wrap;
      margin: 0 auto;
      max-width: 78rem;
      position: relative;
      width: 100%;

      @include mq("44", $type: max) {
         padding: 1rem 1rem 0 1rem;
      }

      @include mq("44") {
         padding: 1rem 2rem 0 2rem;
      }
   }

   h5 {
      display: block;
      font-weight: 600;
      line-height: 1;
      margin: 0 0.1rem 0.5rem 0;
      padding: 0;

      @include mq("38") {
         display: inline-block;
         margin: 0;
         vertical-align: middle;

         &:after {
            content: ": ";
         }
      }
   }

   section {
      display: block;
      line-height: 1.25rem;

      @include mq("33") {
         line-height: 2rem;
      }

      &.dayNightMode {
         align-self: flex-start;
         width: 100%;

         @include mq("60") {
            width: auto;
         }

         select {
            margin-top: 0.75rem;

            @include mq("33") {
               margin-top: unset;
            }
         }
      }

      &.fontFamily,
      &.fontSize {
         align-self: flex-start;
         margin: 0.75rem 0 0 0;
         width: 100%;

         @include mq("60") {
            margin: 0 0 0 1.5rem;
            width: auto;
         }
      }

      &.whatIsThis {
         margin: 0 0 0 auto;

         @include mq("33", $type: max) {
            bottom: 0.88rem;
            margin: 0 0 0 1.5rem;
            position: fixed;
            right: 4rem;
         }

         @include mq("33") {
            margin: 0 0 0 1.5rem;
            position: absolute;
            right: 4rem;
         }

         @media screen and (min-width: 32.9rem) and (max-width: 55rem) {
            right: 4.9rem;
         }

         @include mq("40") {
            right: 4.9rem;
         }
      }
   }

   dialog {
      @include border-radius;
      box-shadow: 0 0 20rem rgba(0, 0, 0, 0.95);
      max-height: 40rem;
      max-width: 30rem;
      width: 100%;

      form {
         margin: 1rem 0 0 0;
         text-align: right;
      }

      h2 {
         margin: 0;
      }

      p {
         line-height: 1.25;
      }

      p:first-child {
         margin: 0;
      }
   }

   span {
      @include border-radius;
      padding: 0 0 0.1rem 0.1rem;
   }

   button {
      font-size: 0.9rem;
      margin: 0.25rem 0 0 0;
      min-width: 4rem;

      @include mq("33") {
         margin: 0 0.1rem 0 0;
         min-width: unset;
      }
   }
}

nav~header {
   align-content: center;
   align-items: center;
   display: flex;
   flex-flow: row nowrap;
   justify-content: center;
   margin-top: 0;
   min-height: 7rem;
   position: relative;
}

main {
   blockquote {
      line-height: inherit;
      margin: 1rem 0 0 0;
      padding: 0.25rem 0.5rem 0.5rem 1rem;
      position: relative;

      @include mq("33") {
         padding: 0.25rem 2rem 0.5rem 1rem;
      }

      &.arcticleSnippet {
         margin: 1rem 0;
         padding: 1rem 0;
      }

      p:first-child {
         margin: 0;
      }
   }

   dl {
      margin: 1rem 0 0 0;
      padding: 0;

      dt {
         font-weight: 600;
         margin: 0;
         padding: 0;
      }

      dd {
         margin: 0.5rem 0 0 0;
         padding: 0;
      }
   }

   article {
      &:not(.home) {
         flex: 1 1 55%;
         position: relative;

         h2 {
            font-size: 200%;
         }

         .date {
            font-style: oblique;
            margin: 0.5rem 0 0 0;
         }

         @include mq("61", $type: max) {
            margin: 0 0 2rem 0;

            &:after {
               content: "";
               height: 1rem;
               position: absolute;
               bottom: -2rem;
               width: 100%;
            }
         }
      }

      h2 {
         margin: 1rem 0 0 0;
         text-align: left;

         @include mq("55") {
            vertical-align: baseline;
         }

         span.date {
            font-weight: 300;
         }
      }

      figure {
         &.articleSnippet {
            align-content: flex-start;
            align-items: stretch;
            display: flex;
            flex-flow: row wrap;
            justify-content: flex-start;
            margin: 1rem 0 0 0;
            overflow: hidden;
            padding: 1rem 0;

            .figureImage {
               margin: 0 1rem 0 0;
               max-width: 24%;
            }

            img {
               @include border-radius;
               align-self: flex-start;
               height: auto;
               max-width: 100%;
            }

            figcaption {
               align-self: flex-start;
               flex: 1 1 70%;
               width: auto;
            }
         }
      }

      .bookSnippet {
         margin: 1rem 0 0 0;

         @include mq("33") {
            display: flex;
            flex: 1 1 auto;
            flex-flow: row nowrap;
         }

         .bookEmbed {
            margin: 0 auto;
            max-width: 25%;
            min-width: 9rem;
         }

         .bookDetails {
            @include mq("33") {
               margin: 0 0 0 1rem;
               width: 72%;
            }
         }
      }
   }

   section {
      &.home {
         article {
            &.home {
               @include mq("33", $type: max) {
                  margin: 1.5rem 0 0 0;
               }

               @include mq("33") {
                  display: grid;
                  grid-column-gap: 2rem;
                  grid-template-columns: [title] 8rem [body] auto;
                  grid-template-rows: auto;
                  margin: 1rem 0 0 0;
               }

               h3 {
                  @include mq("33") {
                     grid-column: title / title;
                  }
               }

               p {
                  @include mq("33") {
                     grid-column: body / body;
                  }
               }
            }

            &:not(.home):nth-last-child(1) {
               margin: 0;

               &:after {
                  background-image: none;
                  bottom: 0;
                  position: relative;
               }
            }
         }
      }
   }

   .hero {
      padding: 0;

      figcaption {
         @include font-family-sans-rounded;
         font-size: 70%;
         opacity: .9;
      }

      img {
         border-radius: 1rem;
         height: auto;
         width: 100%;
      }

      &.home {
         margin: 0;

         @include mq('48') {
            float: right;
            max-width: 50%;
            min-width: 100px;
            padding: 0 0 0 1.5rem;

            figcaption {
               text-align: right;
            }
         }
      }

      &:not(.home) {

         @include mq('48', $type: max) {
            margin: 0;
         }

         @include mq('48') {
            margin: 1rem 0 0 0;
         }

         ~h2 {
            margin: 1.5rem 0 0 0;
         }
      }
   }

   .ephemeral article {
      border-radius: 1rem;
      padding: 0;
   }

   .ephemeral section.body {
      padding: 0 1rem 1rem 1rem;
   }

   .ephemeral-image {
      border-radius: 1rem 1rem 0 0;
      margin: 0;
      padding: 1rem 1rem 0.5rem 1rem;
      text-align: center;
   }

   .ephemeral-image img {
      border-radius: 0.75rem 0.75rem 0.15rem 0.15rem;
      display: block;
      height: auto;
      margin: 0 auto;
      width: 100%;
   }

   aside {
      &:not(.home) {
         align-self: flex-start;
         position: sticky;
         top: 0;
         flex: 1 1 25rem;

         h3 {
            margin: 1rem 0 0 0;
         }
      }

      &.home {
         @include mq("44", $type: max) {
            margin: 1rem 0 0 0;
         }

         @include mq("44") {
            margin: 0.5rem 0 0 0;
         }

         article {
            margin: 1rem 0 0 0;
            
            &.interesting:first-of-type {
               margin-top: 1.5rem;
            }
         }

         hr {
            @include mq("44") {
               display: none;
               visibility: hidden;
            }

            @include mq("44", $type: max) {
               display: block;
               visibility: visible;
            }
         }

         h2 {
            font-size: 120%;
            font-weight: 600;
            line-height: 1.25;
         }

         h3 {
            font-size: 100%;
            margin: 1rem 0 0 0;
         }
      }

      ul {
         margin: 1rem 0 0 0;
         padding: 0 0 0 1.25rem;
      }

      .title {
         font-size: inherit;
         font-weight: inherit;
      }

      &.twitterLink {
         display: flex;
         margin: 1.5rem 0 0 0;
         order: 2;
         position: relative;
         z-index: 2;

         @include mq("44") {
            display: block;
            margin: 0;
            order: 1;
            position: absolute;
            right: 1rem;
            top: 1.5rem;
         }

         iframe {
            border-radius: 4.5rem;
         }
      }
   }

   &.home>.container {
      margin: 0.5rem auto 0 auto;
      max-width: 78rem;
      min-width: 1rem;
      overflow: visible;
      position: relative;
      width: 100%;

      @include mq("44", $type: max) {
         padding: 0 1rem;

      }

      @include mq("44") {
         display: grid;
         gap: 0.5rem 4vw;
         grid-template-columns: [main] minmax(20rem, 60%) [sidebar] 1fr;
         grid-template-rows: [primary] auto [secondary] 1fr;
         padding: 0 2rem;
      }
   }

   &:not(.home) .container {
      display: flex;
      flex-flow: row wrap;
      gap: 3%;
      justify-content: space-between;
      margin: 0.5rem auto 0 auto;
      max-width: 78rem;
      min-width: 1rem;
      overflow: visible;
      position: relative;
      width: 100%;

      @include mq("44", $type: max) {
         padding: 0 1rem;
      }

      @include mq("44") {
         padding: 0 2rem;
      }
   }
}

footer {
   display: block;
   font-size: 90%;
   margin: 1rem 0 0 0;
   overflow: hidden;
   padding: 0 1rem 2rem 1rem;

   @include mq("33") {
      margin: 0 auto;
      padding: 0 0 1rem 0;
   }

   h2,
   h3,
