               background-clip: text;
               -webkit-background-clip: text;
               background-image: linear-gradient(45deg,
                     rgba(0, 0, 0, 0.5) 0%,
                     rgba(0, 0, 0, 0.3) 25%,
                     rgba(0, 0, 0, 0.7) 50%,
                     rgba(0, 0, 0, 0.3) 75%,
                     rgba(0, 0, 0, 0.8) 100%);
               color: transparent;

               &:hover {
                  background-image: linear-gradient(to bottom,
                        rgba(0, 0, 0, 0) 0%,
                        rgba(0, 0, 0, 0) 90%,
                        rgba(0, 0, 0, 0.5) 90.1%,
                        rgba(0, 0, 0, 0.5) 91.9%,
                        rgba(0, 0, 0, 0) 92%,
                        rgba(0, 0, 0, 0) 100%);
                  background-clip: content-box;
                  -webkit-background-clip: content-box;
                  color: inherit;
                  cursor: pointer;
               }
            }
         }

         a[href^="http://"]:not(.button),
         a[href^="https://"]:not(.button) {
            &:after {
               filter: invert(0);
            }
         }

         a:not(.button) {
            &:link {
               color: var(--color-primary);
            }

            &:visited {
               color: var(--color-secondary);

               &:hover {
                  color: var(--color-primary-dark);
               }

               &:active {
                  color: var(--color-primary);
               }
            }

            &:hover {
               color: var(--color-primary-dark);
            }

            &:active {
               color: var(--color-secondary);
            }
         }

         table {
            background: var(--color-gray-4);
            border: 0.1rem solid var(--color-gray-3);

            th {
               border-bottom: 0.1rem solid var(--color-gray-3);
            }
         }

         button,
         .button {
            background: var(--color-gray-1);
            color: var(--color-white);

            @media (hover: hover) {
               &:hover {
                  background-color: var(--color-primary);
                  color: var(--color-white);
               }
            }

            &:active {
               background: var(--color-primary-dark);
               color: var(--color-white);
            }

            &.selected {
               color: var(--color-black);

               @media (hover: hover) {
                  &:hover {
                     color: var(--color-white);
                  }
               }

               &:active {
                  color: var(--color-white);
               }
            }
         }

         .polCom {
            background: var(--color-gray-4);
            border: 0.1rem solid var(--color-gray-3);
         }

         .circled {
            border: 0.1rem solid var(--color-primary-light);
            color: var(--color-gray-1);
         }

         &.home nav {
            color: var(--color-black);
         }

         nav {
            @include mq("60", $type: max) {
               background: linear-gradient(to bottom,
                     var(--color-white-dim) 14rem,
                     hsla(255, 255, 255, 0) 30rem);
            }

            @include mq("60") {
               background: linear-gradient(to bottom,
                     var(--color-white-dim) 5rem,
                     hsla(255, 255, 255, 0) 15rem);
            }
         }

         header {
            color: var(--color-black);
         }

         .newArticles {
            h2 {
               color: var(--color-black);
            }

            div {
               background: linear-gradient(to bottom,
                     var(--color-white),
                     white);
               box-shadow: 0 0 4rem 0.1rem white;
            }
         }

         .archive {
            border-top: 0.1rem solid var(--color-gray-3);
         }

         .ephemeral article {
            background: white;
            border: 0.1rem dotted var(--color-gray-3);
         }

         .ephemeral-image {
            background: var(--color-black-trans-10);
         }

         article.home:not(:first-of-type) {
            border-top: 0.1rem dotted var(--color-gray-3);
         }

         article:not(.home) {
            @include mq("61", $type: max) {
               &:after {
                  background-image: radial-gradient(ellipse at center top,
                        var(--color-gray-3),
                        var(--color-white) 70%);
               }
            }
         }

         .articleSnippet {
            @include border-radius;
            background: var(--color-white);
            border: 0.1rem solid var(--color-gray-2);
            box-shadow: 0 0 1rem var(--color-black-trans10);
            padding: 1rem;
         }

         blockquote:not(.articleSnippet) {
            border-left: 0.2rem double var(--color-gray-2);
         }

         footer {
            .gitHub {
               filter: invert(0);
            }
         }
      }
   }
}

@media print {

   .hideNav,
   nav,
   footer,
   hr,
   button,
   .button,
   .embedtool,
   iframe {
      display: none;
      height: 0;
      visibility: hidden;
      width: 0;
   }

   header,
   main,
   aside {
      margin: 0;
      max-width: unset;
      padding: 0;
   }

   main {
      blockquote {
         border-left: 2px solid black;
      }
   }
}